import { AutomaDifficulty } from "@gaia-project/engine";

const  difficultyData: { [difficulty in AutomaDifficulty]: { ability: string, } } = {
  [AutomaDifficulty.Automalein] : {
    ability : "Decision card 2 is removed, resulting in 1 fewer card in the Automa deck. Automalein starts with 0 VP instead of 10 VP."
  },
  [AutomaDifficulty.Automa] : {
    ability : "No change."
  },
  [AutomaDifficulty.Automachtig] : {
    ability : "Decision card 9 is added to the initial Automa deck. The Automa deck will have 1 more card than normal."
  },
  [AutomaDifficulty.Ultoma] : {
    ability : "Decision cards 9 and 15 are added to the initial Automa deck. The Automa deck will have 2 more card than normal."
  },
  [AutomaDifficulty.Alptrauma] : {
    ability : "Same as Ultoma, but the Automa scores 1 extra VP every time it gains any VP, except during game end scoring."
  }
};

export function difficultyDesc(difficulty: AutomaDifficulty) {
  return `
    <div class='faction-desc'>
      ${difficultyData[difficulty].ability} </br>
    </div>
  `;
}