import Engine, { EngineOptions } from '@gaia-project/engine';
import { Store } from 'vuex';

export interface EngineData extends Engine {
    nextMoveDeadline?: Date;
    lastUpdated?: Date;
}

export interface GameApi {
    replay(store: Store<any>, moveList: string[], options: EngineOptions): EngineData;
}

const api: GameApi = {
    replay(store: Store<any>, moves: string[], options: EngineOptions) {
        try {
            options.spaceShips = options.spaceShips === false ? false : options.spaceShips;
            const engine = new Engine(moves.slice(0, -1), options);

            // reenable fedCheck
            engine.options.noFedCheck = false;

            if (moves.length > 0) {
                engine.move(moves.slice(-1).pop());
            }

            // if against automa, make move
            engine.makeAutomaMove();

            // regenerate commands
            engine.generateAvailableCommandsIfNeeded();

            return engine;
        } catch (err) {
            console.error(err);
            store.commit('error', err);
        }
    },
};

export default api;
