













import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import Booster from './Booster.vue';
import FederationTile from './FederationTile.vue';
import { Booster as BoosterEnum, Expansion } from '@gaia-project/engine';

@Component({
    computed: {
        boosters() {
            return BoosterEnum.values(Expansion.All).filter(key => this.$store.state.gaiaViewer.data.tiles.boosters[key]);
        },
        federations() {
            var federations = this.$store.state.gaiaViewer.data.tiles.federations;
            for (let prop in federations)
                if (federations[prop] < 0)
                    delete federations[prop];
            return federations;
        },
    },
    components: {
        Booster,
        FederationTile,
    },
})
export default class Pool extends Vue {}
