import { Component, Prop } from 'vue-property-decorator';
import { ButtonData } from '../data';

@Component({
})
export default class MoveButton extends Vue {
    private modalShow: boolean = false;
    private customLabel = '';

    modalCancel(arg: string) {
        this.$emit('cancel');
    }

    handleClick() {
        this.modalShow = true;
    }
}
